/**
 *
 * Typography
 *
 */
@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Regular-webfont.woff") format("woff"), url("../fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Semibold-webfont.woff") format("woff"), url("../fonts/OpenSans-Semibold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Bold-webfont.woff") format("woff"), url("../fonts/OpenSans-Bold.ttf") format("truetype");
  font-weight: 800;
  font-style: normal;
}

.headline-base,
.sub-heading-base {
  color: #000;
}

.text-primary {
  color: #9d3304;
}

html,
body {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  background-color: #fff;
}

a {
  color: #3c6792;
}

a:hover {
  color: #507ea4;
}

.modal-body {
  background-image: linear-gradient(180deg, #EFEFEF 25%, #D1D3D4 64%, #D1D3D4 100%);
}

.modal-footer {
  background-color: #D1D3D4;
}

.modal-header .close {
  color: #3c6792;
  opacity: 1;
}

.modal-header .close:hover {
  color: #305375;
}

/* ----------------------------------------------------------
//  Actions
// ---------------------------------------------------------- */
.cta-banner {
  background-color: #3c6792;
  color: #fff;
  display: flex;
  justify-content: center;
}

.cta-banner a {
  color: inherit;
}

.btn {
  border-radius: 6px;
}

.btn-primary {
  background-color: #5bb07a;
  border-color: #5bb07a;
  color: #fff;
  outline: 0;
}

.btn-primary:hover, .btn-primary:focus {
  background-color: transparent;
  border-color: #a0d1b2;
  color: #5bb07a;
  outline: 0;
}

/*=========================================
=      Header CTA Banner & Navigation       =
=========================================*/
.agent {
  background: #507ea4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.agent:after {
  border-left-color: #507ea4;
}

.header-cta-banner {
  display: none;
  justify-content: center;
  background-color: #3c6792;
  color: white;
}

.header-cta-banner a {
  color: #fff;
}

.site-header .navbar-toggle {
  color: #3c6792;
}

.site-header .navbar-toggle a {
  color: inherit;
}

@media only screen and (max-width: 62em) {
  .navbar .flex-nav a {
    font-weight: normal;
  }
  .navbar .flex-nav li a {
    border-top: 1px solid #ebebeb;
  }
  .navbar .flex-nav li a:focus, .navbar .flex-nav li a:active, .navbar .flex-nav li a:hover {
    background-color: #3c6792;
    border-top-color: #3c6792;
    color: #fff;
  }
}

.nav-primary a {
  color: #3c6792;
}

.nav-primary a:hover {
  color: #3c6792;
}

.nav-primary li:after {
  color: #3c6792;
}

.order-status-nav a {
  color: #3c6792;
}

.order-status-nav a:hover {
  color: #3c6792;
}

.navbar-cart .badge {
  border-color: #3c6792;
  color: #3c6792;
}

/**
 *
 * Forms.
 *
 */
.form-control {
  border-radius: 0;
}

.form-control[placeholder] {
  font-style: italic;
}

.email-signup {
  border-color: rgba(255, 255, 255, 0.5);
  text-align: center;
}

