/* ----------------------------------------------------------
//  Hero section
// ---------------------------------------------------------- */
.hero {
  background-image: none;
  background: #fff;
  border-top: 1px solid #ebebeb;
}

.node-page .hero,
.node-error-page .hero {
  background-color: #5bb07a;
}

.node-page .hero .headline-base,
.node-error-page .hero .headline-base {
  color: #fff;
}

/* ----------------------------------------------------------
//  Online Checkout Landing Pages (Formerly Category pages)
// ---------------------------------------------------------- */
.hero-category {
  background-color: #f8f8f8;
  border-top: 1px solid silver;
  padding: 0;
}

@media only screen and (max-width: 30em) {
  .hero-category .hero-image-embed {
    display: none;
  }
  .hero-category .hero-image-cta {
    margin-top: 0;
  }
}

.hero-image-headline__title {
  color: #3c6792;
}

.hero-image-cta {
  border: 0;
}

@media only screen and (min-width: 75em) {
  .hero-image-cta .btn-view-offers {
    font-size: 20px;
    font-weight: bold;
    padding: 14px 64px;
  }
}

.hero-cta__telgroup a,
.hero-image-cta__telgroup a {
  color: #fff;
}

.hero-image-cta__title {
  color: #5A5A5A;
}

.hero-image-cta__divider {
  color: #5A5A5A;
}

/* ----------------------------------------------------------
//  Call-in Landing Pages 
// ---------------------------------------------------------- */
.hero-product__price {
  background-color: #9d3304;
  color: #fff;
}

.hero-product__price:after {
  border-color: transparent;
  border-left-color: #9d3304;
}

.hero-product__price .filter-price,
.hero-product__price .filter-label,
.hero-product__price .filter-label-inline {
  color: #fff;
}

.hero-product__features h4 {
  color: #fff;
}

.hero-product__features ul {
  color: #ebebeb;
}

.hero-promo__text {
  color: #fff;
}

.hero-cta {
  color: #fff;
}

.hero-cta__link {
  color: #3c6792;
}

.hero-cta__link:hover, .hero-cta__link:focus {
  color: #3c6792;
}

