=            Frontpage            =
=============================================*/
@media only screen and (max-width: 62em) {
  .brand-value {
    border-bottom: 1px solid #ebebeb;
  }
}

.filter {
  color: #000;
  background-color: #fff;
  border-color: #5bb07a;
  border-radius: 3px;
  min-height: 324px;
  padding: 0 2em;
}

.filter .btn-view-offers {
  font-weight: normal;
}

@media only screen and (min-width: 47.875em) and (max-width: 62em) {
  .filter {
    padding: 0 1em;
  }
}

.filter-header {
  border-bottom: 2px solid #ebebeb;
}

.filter-price {
  color: #3c6792;
  font-size: 32px;
  font-weight: normal;
}

@media only screen and (min-width: 47.875em) and (max-width: 62em) {
  .filter-price {
    font-size: 20px;
  }
}

.filter-price div {
  border: 0;
}

.filter-label,
.filter-label-inline {
  color: #999;
}

.filter-feature-group .filter-feature:not(:last-of-type) {
  border-right: 2px solid #ebebeb;
}

@media only screen and (min-width: 47.875em) {
  .filter-promoted .filter-footer {
    border-top: 2px solid #ebebeb;
  }
}

.filters-disclaimer {
  color: #9d3304;
  margin-top: 1.5em;
}

.node-type-results-page .filters-disclaimer {
  font-size: 14px;
  font-weight: bold;
}

@media only screen and (max-width: 47.875em) {
  .node-type-results-page .filters-disclaimer {
    text-align: center;
  }
}

.filter-title {
  font-size: 24px;
  font-weight: normal;
}

@media only screen and (min-width: 47.875em) and (max-width: 62em) {
  .filter-title {
    font-size: 20px;
  }
}

.filter-desc {
  margin: 0.75em 0;
}

.filter-header {
  border-bottom-width: 1px;
}

/* -------------------------------------------
//  Search results call-in overlay
// -------------------------------------------*/
.call-overlay .modal-content {
  background-image: linear-gradient(0deg, #5bb07a 43%, #fff 43%);
}

.call-overlay .modal-header {
  background: url(../img/call-in-popup-agent-min.png) center 18% no-repeat;
}

.call-overlay .modal-body {
  background-image: none;
  background-color: #fff;
  border-color: #5bb07a;
}

.call-overlay .modal-footer {
  background-color: #5bb07a;
  color: #fff;
}

.call-overlay__label {
  background: #fff;
  color: #5A5A5A;
}

.call-overlay__promo {
  color: #5bb07a;
}

/* ----------------------------------------------------------
//  Search pop-up
// ---------------------------------------------------------- */
.block-ac-results .block-content-area {
  background-color: #fff;
}

.block-ac-results .block-content-area .block-header .block-header__link span {
  border-bottom-color: #3c6792;
}

.block-ac-results .block-content-area .block-header .block-header__link i {
  color: #3c6792;
}

.block-ac-results .block-content-area .block-header .block-header__link:hover span {
  color: #3c6792 !important;
}

.block-ac-results .block-content-area .block-body .block-svg-icon .st10, .block-ac-results .block-content-area .block-body .block-svg-icon .st12, .block-ac-results .block-content-area .block-body .block-svg-icon .st15 {
  stroke: #3c6792;
}

.block-ac-results .block-content-area .block-body .block-svg-icon .st11, .block-ac-results .block-content-area .block-body .block-svg-icon .st14 {
  fill: #3c6792;
}

.block-ac-results .block-content-area .block-body .block-body__callout {
  color: #3c6792;
}

.block-ac-results .block-content-area .block-footer {
  border-color: #3c6792;
}

.block-ac-results .block-content-area .block-footer .block-footer__phone {
  color: #3c6792;
}

.block-ac-results .block-content-cover {
  background-color: #3c6792;
}

.block-ac-results.closed .block-content-wrap .block-content-area {
  background-color: #3c6792;
}

#filterContainer .filters-wrap {
  border: 1px solid #ebebeb;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  font-size: 13px;
  margin-bottom: 2em;
}

#filterContainer .filter-title {
  background-color: #3c6792;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  font-size: 13px;
  padding: 1em;
}

#filterContainer .filter-title h4 {
  color: #fff;
  display: inline;
  font-size: inherit;
  font-weight: normal;
  margin: 0;
  text-transform: uppercase;
}

#filterContainer .filter-title a {
  color: #fff;
  font-weight: bold;
  text-decoration: underline;
  float: right;
}

@media only screen and (max-width: 62em) {
  #filterContainer .filter-title a {
    color: #3c6792;
  }
}

#filterContainer h5 {
  font-size: 13px;
  text-transform: uppercase;
  margin-top: 2em;
  margin-bottom: 1.5em;
}

#filterContainer .filter-row {
  padding-left: 1em;
}

#filterContainer .filter-row:not(:last-child) {
  padding-bottom: 0 !important;
}

#filterToggleOn {
  background-color: #9d3304;
  border: 0;
  text-transform: uppercase;
}

#filterToggleOn:hover {
  color: #fff;
}

@media only screen and (min-width: 47.875em) and (max-width: 62em) {
  #filterToggleOn {
    border-radius: 6px;
    position: absolute;
    right: 0;
    margin: 0 1em;
  }
}

.DimensionValue input {
  outline: 0;
}

.DimensionValue input[type=checkbox]:checked + label {
  color: #3c6792;
}

/* ----------------------------------------------------------
//  Plan results.
// ---------------------------------------------------------- */
.alert {
  margin-top: 2em;
  margin-bottom: 0;
  text-align: center;
}

.alert button {
  padding: 0 1em;
}

.alert strong, .alert p {
  color: #3c6792;
}

.changeLocContents {
  background-color: #5bb07a;
  color: #fff;
}

.locationAddress a {
  border-color: #fff;
  color: #fff;
}

#resultLabels {
  color: #9f9c9d;
  font-size: 13px;
}

#resultLabels .fa {
  color: #3c6792;
  font-size: 2em;
  line-height: 18px;
  margin-left: 0.75em;
  vertical-align: top;
}

.results-container {
  padding: 0.75em 0;
}

@media only screen and (min-width: 47.875em) {
  .results-container {
    padding: 2.5em 0;
  }
}

.result {
  border: 1px solid #ebebeb;
  font-size: 13px;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  position: relative;
}

@media only screen and (max-width: 47.875em) {
  .result {
    max-width: 23.44em;
  }
}

.result .col {
  padding: 1.25em;
}

@media only screen and (min-width: 47.875em) {
  .result .col {
    height: 170px;
  }
  .result .col:not(:first-child) {
    border-left: 1px solid #ebebeb;
  }
}

.result__more {
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  padding: 1.25em;
  text-align: center;
}

@media only screen and (min-width: 47.875em) {
  .result__more {
    border: 0;
    position: absolute;
    top: 100px;
    text-align: left;
  }
